<template>
  <div class="footer-container">
    <Header>
      <template #leftTitle>
        {{t('index.我的團隊')}}
      </template>
    </Header>
    <div class="team">
      <div class="cell-2 region-bg" style="margin-bottom:12px;">
        <div class="key">{{t('index.團隊人數')}}</div>
        <div class="value">{{staticNum.netnum}}</div>
      </div>
      <!-- <div class="cell-2 region-bg" style="margin-bottom:12px;">
        <div class="key">{{t('index.團隊買入量')}}</div>
        <div class="value">{{staticNum.buynum}} <span>TOP</span></div>
      </div> -->
      <div class="cell-2 region-bg" style="margin-bottom:12px;">
        <div class="key">{{t('index.團隊獎勵')}}</div>
        <div class="value">{{teamRewards}} <span>USDT</span></div>
      </div>
      <div class="record">
        <!-- <div class="table-title">{{t('index.團隊列表')}}</div> -->
        <div class="table region-bg">
          <div class="title">{{t('index.團隊列表')}}</div>
          <van-list class="tbody" v-model:loading="loading" :finished="finished" :immediate-check="false" :finished-text="t('index.暫無更多數據')" @load="onLoad">
            <div class="tr" v-for="(item,index) in lists" :key="index">
              <!-- <span>{{index+1}}.</span> -->
               {{item.substring(0,12)}}...{{item.substring(item.length - 15, item.length)}}
            </div>
          </van-list>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
// import { myNet } from '@/api'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { config } from "@/utils/config";
import introAbi from "@/utils/introAbi";
import idoAbi from "@/utils/idoAbi";
import BigNumber from "bignumber.js";
import {
  Toast
} from "vant";
export default {
  components: {
    Header,
    Footer
  },
  setup () {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      loading: false,
      finished: true,
      page: 1,
      limit: 10,
      lists: [],
      staticNum: { netnum: '0', buynum: '0.00' },
      teamRewards: '0.00',
      introContract: null,
      idoContract: null,
      initContract: () => {
        data.introContract = new data.web3.eth.Contract(
          introAbi,
          config.topAddress
        );
        data.idoContract = new data.web3.eth.Contract(
          idoAbi,
          config.IDO
        );
        data.initData()
      },
      initData: () => {
        data.getData()
      },
      onLoad: async () => {
        data.page = data.page + 1;
        await data.getData();
      },
      getData: async () => {
        data.loading = true;
        let userIdoInfo = await data.idoContract.methods
          .getUserIdo(data.address)
          .call({ from: data.address });
        console.log(userIdoInfo, 'getUserIdo')
        data.teamRewards = new BigNumber(userIdoInfo._totalRewards)
            .dividedBy(10 ** 18)
            .toFixed(2);
        data.staticNum.netnum = userIdoInfo._inviteNumber;

        data.lists = await data.idoContract.methods
          .getDirectTeam(data.address)
          .call({ from: data.address });
        console.log(data.lists)

        data.finished = true;
        data.loading = false;
      },
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
</style>
<style lang="less" scoped>
.team {
  padding-bottom: 16px;
  .cell-2 {
    padding: 0 20px;
    .key {
      padding-top: 24px;
      font-size: 15px;
      font-weight: 500;
      color: #00edf8;
    }
    .value {
      word-break: break-all;
      padding-top: 20px;
      font-size: 22px;
      font-weight: 600;
      color: #ffffff;
      padding-bottom: 25px;
      span {
        font-size: 14px;
      }
    }
  }
  .record {
    .table-title {
      width: 220px;
      height: 31px;
      background: url(~@/assets/images/table-title.webp) no-repeat;
      background-size: 100% 100%;
      text-align: center;
      margin: 0 auto 17px;
      position: relative;
      line-height: 31px;
      font-size: 16px;
      font-weight: 600;
      color: #00edf8;
      text-shadow: 0px 1px 0px rgba(73, 0, 146, 0.35);
    }
    .table {
      padding: 47px 23px 25px;
      .tbody {
        .tr {
          display: flex;
          align-items: center;
          font-size: 14px;
          color: #fff;
          justify-content: center;
          &:not(:last-child) {
            margin-bottom: 19px;
          }
          span {
            color: #5bffff;
          }
        }
      }
    }
  }
}
</style>
